@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 40px 0px 40px 0px;
}

button {
    height: 35px;
    width: 175px;
    margin-left: 10px;

}

#aStarBtn {
    margin-right: 10px;
}

#resetBtnTwo {
    margin-right: 10px;
}

.grid {
    margin: 40px 0 40px 0;
}

.resetBtn { 
    margin-bottom: 20px;
}

.instruction-wrapper { 
    margin: 50px 0px 0px 0px;
    padding: 20px 20% 50px 20%;
    background-color: rgb(234, 242, 248);
}

.node {
    width: 20px;
    height: 20px;
    outline: 1px solid rgba(221, 221, 221);
    display: inline-block;
}

.node-start {
    background-color: rgb(255, 215, 0);
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: cover;
}

.node-finish {
    background-color: rgb(236, 77, 77);
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: cover;
}

.node-wall {
    background-color: rgb(12, 53, 71);
}

.node-visited {
    -webkit-animation-name: visitedAnimation;
            animation-name: visitedAnimation;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
    0% {
        transform: scale(0.3);
        background-color: rgba(236, 77, 77, 0.75);
        border-radius: 100%;
    }

    50% {
        background-color: rgba(236, 100, 77, 0.75);
    }

    75% {
        transform: scale(1.2);
        background-color: rgba(255, 145, 55, 0.75);
    }

    100% {
        transform: scale(1);
        background-color: rgba(30, 199, 221);
    }
}

@keyframes visitedAnimation {
    0% {
        transform: scale(0.3);
        background-color: rgba(236, 77, 77, 0.75);
        border-radius: 100%;
    }

    50% {
        background-color: rgba(236, 100, 77, 0.75);
    }

    75% {
        transform: scale(1.2);
        background-color: rgba(255, 145, 55, 0.75);
    }

    100% {
        transform: scale(1);
        background-color: rgba(30, 199, 221);
    }
}

.node-path {
    -webkit-animation-name: pathAnimation;
            animation-name: pathAnimation;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
    -webkit-animation-delay: 0;
            animation-delay: 0;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}

@-webkit-keyframes pathAnimation {
    0% {
        transform: scale(0.6);
        background-color: rgb(236, 77, 77);
    }

    50% {
        transform: scale(1.2);
        background-color: rgb(236, 77, 77);
    }

    100% {
        transform: scale(1);
        background-color: rgb(236, 77, 77);
    }
}

@keyframes pathAnimation {
    0% {
        transform: scale(0.6);
        background-color: rgb(236, 77, 77);
    }

    50% {
        transform: scale(1.2);
        background-color: rgb(236, 77, 77);
    }

    100% {
        transform: scale(1);
        background-color: rgb(236, 77, 77);
    }
}
